:root {
  --bronze: #cd7f32;
  --side-nav-width: 20%;
  --main-bg: #111;
}

body {
  height: 100vh;
  background-color: var(--main-bg);
  color: #fff;
  margin: 0;
  padding: 0;
  font-family: Manrope, sans-serif;
  overflow: hidden;
}

main {
  width: calc(100% - var(--side-nav-width) * 2);
  overflow-y: auto;
}

ul {
  text-align: center;
  padding: 0;
  list-style: none;
}

li {
  color: #fff;
  text-align: center;
  vertical-align: center;
  list-style: none;
}

#transparentBG {
  width: 100%;
  height: 100%;
  background: rgba(0 0 0 50%);
  z-index: 6968;
  display: none;
  position: absolute;
}

#confirmationBox {
  z-index: 6969;
  width: 30%;
  height: 30%;
  background-color: #141414;
  border: 1px solid gold;
  border-radius: 5px;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#confirmWrapper, #alertWrapper, #promptWrapper {
  height: 100%;
  display: none;
}

#confirm > p, #alert > p, #prompt > label {
  text-align: center;
  grid-area: txt;
  margin-left: 15px;
  margin-right: 15px;
}

#okConfirm, #okAlert, #okPrompt {
  grid-area: btn;
}

#cancelConfirm, #cancelPrompt {
  grid-area: btn2;
}

#promptText {
  grid-area: lbl;
}

#confirm > button, #alert > button, #prompt > button {
  width: 75%;
  height: 50%;
  color: #fff;
  background-color: #171717;
  border: 1px solid gold;
  margin: 0 auto;
  transition-duration: .15s;
}

#confirm > button:hover, #alert > button:hover, #prompt > button:hover {
  background-color: #333;
}

#confirm {
  height: 100%;
  grid-template: "txt txt txt txt" 75%
                 ". btn btn2 ." 25%
                 / 25% 25% 25% 25%;
  display: grid;
}

#alert {
  height: 100%;
  grid-template: "txt txt txt" 75%
                 ". btn ." 25%
                 / 33.3333% 33.3333% 33.3333%;
  display: grid;
}

#prompt {
  height: 100%;
  grid-template: ". . . ." 5%
                 "txt txt txt txt" 55%
                 ". lbl lbl ." 10%
                 ". . . ." 5%
                 ". btn btn2 ." 25%
                 / 25% 25% 25% 25%;
  display: grid;
}

.mainProgressBarArea {
  width: 100%;
  border-top: 2px solid red;
  border-bottom: 2px solid #fff;
  flex-direction: row;
  display: flex;
}

.tabSeparator {
  width: var(--side-nav-width);
  color: #ff0;
  text-align: center;
  margin-top: 15px;
  margin-bottom: 15px;
}

.progressBarBig {
  height: 50px;
  width: 70%;
  border: 2px solid #fff;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 5%;
  position: relative;
}

.progressBarBigText {
  height: 25px;
  z-index: 10;
  text-align: center;
  background-color: #000;
  border: 2px solid #fff;
  border-top-left-radius: 20%;
  border-top-right-radius: 20%;
  padding-left: 5px;
  padding-right: 5px;
}

#progression {
  width: 5%;
  height: 100%;
  background-color: purple;
}

#smallResourceIndicators {
  font-variant-numeric: tabular-nums;
  display: flex;
  position: absolute;
  bottom: 0;
  right: 0;
}

#smallResourceIndicators > div {
  display: inline-block;
}

#perSecCurr {
  color: gold;
}

#perSecPrev {
  color: var(--bronze);
}

#coinWorth {
  color: gold;
}

aside.sideNav {
  max-width: var(--side-nav-width);
  border-right: 1px solid #ededed;
  flex: 1 0;
}

.wrapper {
  height: 100%;
  flex-direction: row;
  display: flex;
}

@media (max-width: 500px) {
  .wrapper {
    flex-direction: column-reverse;
  }

  ul {
    justify-content: space-between;
    align-items: center;
    display: flex;
  }
}

#resourceIndicators, .wrapperInner {
  height: 475px;
  margin: 50px;
  position: relative;
}

#innerTesting {
  overflow-y: visible;
}

.tabColumn {
  width: 100%;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
  left: 0;
}

.tab {
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
  justify-content: space-between;
  padding: 16px;
}

.tab:hover {
  cursor: pointer;
  background-color: gray;
  transition-duration: .15s;
}

.tabs {
  text-align: center;
}

.coinUpgrades {
  flex-direction: row;
  justify-content: center;
  display: flex;
}

.coinUpgrade {
  text-align: center;
  border: 2px solid gold;
  flex-direction: column;
  flex-grow: 1;
  flex-basis: 50%;
  align-items: center;
  margin-bottom: 10px;
  margin-left: 10px;
  margin-right: 10px;
  padding: 12px;
  display: flex;
}

.coinUpgradeText {
  height: auto;
  margin: auto;
}

.coinUpgrade > button {
  height: 20px;
  width: 150px;
  cursor: pointer;
  background-color: var(--main-bg);
  color: #fff;
}

#resourceIndicators {
  flex-direction: column;
  margin-left: 0;
  display: flex;
}

#resourceIndicators > div {
  flex: 1 0 auto;
  margin: 5% 10%;
}

.talentContainer {
  border: 2px solid orchid;
  flex-direction: row;
  justify-content: center;
  padding: 12px;
  display: flex;
}

.talentContainer > p {
  height: auto;
  margin: auto;
}

.talentBarContainer {
  width: 70%;
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.talentProgressBar {
  width: 100%;
  height: 40px;
  vertical-align: middle;
  border: 2px solid #fff;
  position: relative;
}

.talentSacrificeArea {
  flex-direction: row;
  justify-content: left;
  display: flex;
}

.talentBarText {
  width: 150px;
  height: 25px;
  z-index: 10;
  text-align: center;
  word-break: break-all;
  background-color: #000;
  border: 2px solid #fff;
  border-top-left-radius: 20%;
  border-top-right-radius: 20%;
  position: absolute;
  bottom: 0;
  right: 0;
  overflow: hidden;
}

.talentProgression {
  height: 100%;
  width: 5%;
  background-color: #0ff;
}

.talentSacrifice {
  width: 40%;
  height: 40px;
  cursor: pointer;
  background-color: var(--main-bg);
  color: orchid;
  margin-right: 10px;
}

.talentInfo {
  width: 30%;
  flex-direction: column;
  display: flex;
}

.talentInfoText {
  height: auto;
  text-align: left;
  width: 90%;
  margin: auto;
}

.mainBattleContainer {
  min-width: 100%;
  flex-direction: row;
  justify-content: center;
  margin-top: 4.5%;
  display: flex;
}

.duelingStatArea {
  align-items: right;
  border: 2px solid #fff;
  flex-direction: column;
  margin: 10px;
  display: flex;
}

.duelingProgressBar {
  position: relative;
}

.duelingBarText {
  height: 50%;
  width: 100%;
  z-index: 10;
  text-align: center;
  vertical-align: center;
  position: absolute;
  bottom: 25%;
  overflow: hidden;
}

.duelingBarProgression {
  width: 100%;
  height: 100%;
}

.hpBar {
  width: 250px;
  height: 40px;
  border: 2px solid gray;
}

.mpBar {
  width: 250px;
  height: 40px;
  border: 2px solid #fff;
}

.hpBarProgression {
  background-color: red;
}

.mpBarProgression {
  background-color: #00008b;
}

#enemyHPProgression, #enemyMPProgression {
  align-self: right;
}

.statSummary {
  text-align: right;
  margin-top: 12%;
  margin-bottom: 12%;
  margin-right: 30%;
}

.statSummary > p {
  height: auto;
  margin: auto;
}

#playerMoves {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.moveButton {
  height: 30px;
  width: 175px;
}

@media screen and (max-width: 1130px) {
  #coinUpgrades {
    min-height: auto;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
  }

  .coinUpgrade {
    width: 33.3333%;
  }

  .wrapperInner {
    height: auto;
  }

  .coinUpgrade > button {
    width: 80%;
    word-break: break-all;
    overflow: hidden;
  }
}

img {
  cursor: pointer;
}

